import { Component, OnInit } from '@angular/core';
import { Deck } from '../deck';

@Component({
  selector: 'app-bunch',
  templateUrl: './bunch.component.html',
  styleUrls: ['./bunch.component.css']
})
export class BunchComponent implements OnInit {

  bunchOfStats: any;

  constructor() { }

  ngOnInit() {
  }

  generateABunch() {
    this.bunchOfStats = {
      n: 0,
      nCreature: [],
      countNCreature: function(n) {
        return this.nCreature.filter(m => m === n).length;
      },
      avgCreature: function() {
        const sum = this.nCreature.reduce((acc, n) => (acc + n), 0);
        return (sum / this.nCreature.length).toFixed(1);
      }
    };
    for (let i = 0; i < 1000; i++) {
      let d: Deck = new Deck();
      let nCreature: number;
      do {
        // d = this.generateDeck();
        nCreature = d.countCardsByType('Creature');
      } while (nCreature < 13 || nCreature > 18);
      this.bunchOfStats.n = i;
      this.bunchOfStats.nCreature.push(nCreature);
    }
  }

}
