import { Card, Color } from './card';

export class Deck {
  colors: Color[];
  cards: Card[] = [];

  getName(): string {
    return this.colors.join('-');
  }

  getNumNonLands(): number {
    return this.cards.reduce((acc, c: Card) => {
      return c.OracleType === 'Land' ? acc : acc + c.NumUsed;
    }, 0);
  }

  getNumCards(): number {
    return this.cards.reduce((acc, c: Card) => acc + c.NumUsed, 0);
  }

  getCardsByType(type: string): Card[] {
    return this.cards
      .filter(c => c.Type === type)
      .sort((a: Card, b: Card) => {
        if (a.CMC === b.CMC) {
          return 0;
        }
        return a.CMC - b.CMC;
      });
  }

  countCardsByCMC(cost: number): number {
    let filtered: Card[];
    if (cost >= 7) {
      filtered = this.cards.filter(c => c.CMC >= cost);
    } else {
      filtered = this.cards.filter(c => c.CMC === cost);
    }
    return filtered.reduce((acc, c: Card) => acc + c.NumUsed, 0);
  }

  countCardsByColor(): { White: number, Blue: number, Black: number, Red: number, Green: number } {
    const ret = { White: 0, Blue: 0, Black: 0, Red: 0, Green: 0 };
    const colors: string[] = ['White', 'Blue', 'Black', 'Red', 'Green'];
    this.cards.reduce((acc, c: Card) => {
      if (colors.includes(c.Color)) {
        acc[c.Color] += 1;
      }
      return acc;
    }, ret);
    return ret;
  }

  countCardsByType(type: string): number {
    return this.getCardsByType(type).reduce((acc, c: Card) => acc + c.NumUsed, 0);
  }

  toText(): string {
    const out: string[] = [ this.getName() ];
    this.cards.forEach((c: Card) => {
      out.push(c.NumUsed + ' ' + c.Name);
    });
    return out.join('\n');
  }

  toDck(): string {
    const out: string[] = [
      '[metadata]',
      'Name=' + this.getName(),
      '[general]',
      'Sealed',
      '[main]'
    ];
    this.cards.forEach((c: Card) => {
      out.push(c.NumUsed + ' ' + c.Name);
    });
    return out.join('\n');
  }
}

