import { Component } from '@angular/core';
import { Deck } from './deck';
import { Card, Color } from './card';
import { SoiBlock } from '../data/soi-block';
import * as JSZip from 'jszip';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  decks: Deck[];

  getNewDecks() {
    const deckColors: Color[][] = [
      [Color.White, Color.Blue],
      [Color.White, Color.Black],
      [Color.White, Color.Red],
      [Color.White, Color.Green],
      [Color.Blue, Color.Black],
      [Color.Blue, Color.Red],
      [Color.Blue, Color.Green],
      [Color.Black, Color.Red],
      [Color.Black, Color.Green],
      [Color.Red, Color.Green]
    ];

    this.shuffle(deckColors);
    let cards: Card[] = SoiBlock;
    this.shuffle(cards);

    const decks: Deck[] = [];
    const zip = new JSZip();
    for (let i = 0; i < 8; i++) {
      const deck: Deck = new Deck();

      deck.colors = deckColors[i];
      deck.colors.sort();

      const onColor: Card[] = cards.filter(c => {
        const c1: string = deck.colors[0].toString();
        const c2: string = deck.colors[1].toString();
        if (c1 === c.Color) {
          return true;
        }
        if (c2 === c.Color) {
          return true;
        }
        if (c1 + ' ' + c2 === c.Color) {
          return true;
        }
        if (c.Color === 'Colorless') {
          return true;
        }
      });
      const creatures = onColor.filter(c => c.Type === 'Creature');
      const other = onColor.filter(c => c.Type !== 'Creature');

      const desiredCreatures: number = Math.floor(Math.random() * (18 - 14 + 1)) + 14;
      do {
        const card: Card = creatures.pop();
        card.NumUsed = Math.min(card.Use, desiredCreatures - deck.getNumNonLands());
        deck.cards.push(card);
      } while (deck.getNumNonLands() < desiredCreatures);

      do {
        const card: Card = other.pop();
        card.NumUsed = Math.min(card.Use, 23 - deck.getNumNonLands());
        deck.cards.push(card);
      } while (deck.getNumNonLands() < 23);

      const numBasicsNeeded = 40 - deck.getNumCards();
      const countByColor = deck.countCardsByColor();
      const basics = {
        White: 'Plains',
        Blue: 'Island',
        Black: 'Swamp',
        Red: 'Mountain',
        Green: 'Forest'
      };
      let isFirstColor = true;
      ['White', 'Blue', 'Black', 'Red', 'Green'].forEach(color => {
        const count: number = countByColor[color];
        if (count > 0) {
          let proportion: number = count / 23;
          proportion = Math.max(proportion, 7 / 17);
          proportion = Math.min(proportion, 10 / 17);
          deck.cards.push({
            Name: basics[color],
            NumUsed: isFirstColor ? Math.round(proportion * numBasicsNeeded) : 40 - deck.getNumCards()
          } as Card);
          isFirstColor = false;
        }
      });


      const names: string[] = deck.cards.reduce((acc, card) => acc.concat(card.Name), []);
      cards = cards.filter(c => !names.includes(c.Name));
      decks[i] = deck;

      zip.file(deck.getName() + '.dck', deck.toDck());
    }

    this.decks = decks;

    zip.generateAsync({type: 'blob'}).then(blob => {
      const url = window.URL.createObjectURL(blob);
      window.open(url);
    });
  }

  // creatures: 15-18
  // curve (creatures): 1: 0, 2: 4-6, 3: 6-7, 4: 3-4, 5: 2-3, 6: 1-2
  // creatures: 14-17
  // curve: 1: 0-3, 2: 4-5, 3: 7, 4: 6, 5+: 4
  // creatures: 14-17
  // curve (creatures): 1: 0-2, 2: 2-6, 3: 3-5, 4: 2-4, 5: 1-3, 6+: 0-2

  private shuffle(arr) {
    for (let i = arr.length - 1; i > 0; i--) {
      const j = Math.floor(Math.random() * (i + 1));
      [arr[i], arr[j]] = [arr[j], arr[i]];
    }
  }
}
